import React from 'react';
import { TableGridSwitch } from '@wix/patterns';
import { TableGridSwitchConfig } from '../../types';
import { useTableGridSwitchFeatures } from '../../hooks/useTableGridSwitchFeatures';

export interface AutoPatternsTableGridSwitchProps {
  configuration: TableGridSwitchConfig;
}

export const AutoPatternsTableGridSwitch = ({
  configuration,
}: AutoPatternsTableGridSwitchProps) => {
  const props = useTableGridSwitchFeatures(configuration);
  return (
    <TableGridSwitch dataHook="auto-patterns-table-grid-switch" {...props} />
  );
};
